/* Gradients / Overlays
-------------------------------------------------------*/
.bg-white-gradient:before {
	content: "";
	top: 0;
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, 0.5);
	/* Old browsers */
	background: -moz-linear-gradient(bottom, rgba(255, 255, 255, 0.5) 0%, #fff 100%);
	/* FF3.6+ */
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(255, 255, 255, 0.5)), color-stop(100%, #fff));
	/* Chrome,Safari4+ */
	background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.5) 0%, #fff 100%);
	/* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(bottom, rgba(255, 255, 255, 0.5) 0%, #fff 100%);
	/* Opera 11.10+ */
	background: -ms-linear-gradient(bottom, rgba(255, 255, 255, 0.5) 0%, #fff 100%);
	/* IE10+ */
	background: linear-gradient(to top, rgba(255, 255, 255, 0.5) 0%, #fff 100%);
	/* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#000000',GradientType=0 );
	/* IE6-9 */ }

.bg-overlay {
	position: relative; }
	.bg-overlay:before {
		content: "";
		top: 0;
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
